.uk-close::-moz-focus-inner {
  border: 0;
  padding: 0; }

.uk-close {
  -webkit-appearance: none;
  margin: 0;
  border: none;
  overflow: visible;
  font: inherit;
  color: inherit;
  text-transform: none;
  padding: 0;
  background: 0 0;
  display: inline-block;
  box-sizing: content-box;
  width: 20px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  opacity: .3; }

.uk-close:after {
  display: block;
  content: "\f00d";
  font-family: FontAwesome; }

.uk-close:focus, .uk-close:hover {
  opacity: .5;
  outline: 0;
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

.uk-close-alt {
  padding: 2px;
  border-radius: 50%;
  background: #eee;
  opacity: 1; }

.uk-close-alt:focus, .uk-close-alt:hover {
  opacity: 1; }

.uk-close-alt:after {
  opacity: .5; }

.uk-close-alt:focus:after, .uk-close-alt:hover:after {
  opacity: .8; }

.uk-badge {
  display: inline-block;
  padding: 0 5px;
  background: #00a8e6;
  font-size: 10px;
  font-weight: 700;
  line-height: 14px;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  text-transform: none; }

a.uk-badge:hover {
  color: #fff; }

.uk-badge-notification {
  box-sizing: border-box;
  min-width: 18px;
  border-radius: 500px;
  font-size: 12px;
  line-height: 18px; }

.uk-badge-success {
  background-color: #8cc14c; }

.uk-badge-warning {
  background-color: #faa732; }

.uk-badge-danger {
  background-color: #da314b; }

.uk-alert {
  margin-bottom: 15px;
  padding: 10px;
  background: #ebf7fd;
  color: #2d7091; }

* + .uk-alert {
  margin-top: 15px; }

.uk-alert > :last-child {
  margin-bottom: 0; }

.uk-alert h1, .uk-alert h2, .uk-alert h3, .uk-alert h4, .uk-alert h5, .uk-alert h6 {
  color: inherit; }

.uk-alert > .uk-close:first-child {
  float: right; }

.uk-alert > .uk-close:first-child + * {
  margin-top: 0; }

.uk-alert-success {
  background: #f2fae3;
  color: #659f13; }

.uk-alert-warning {
  background: #fffceb;
  color: #e28327; }

.uk-alert-danger {
  background: #fff1f0;
  color: #d85030; }

.uk-alert-large {
  padding: 20px; }

.uk-alert-large > .uk-close:first-child {
  margin: -10px -10px 0 0; }

.uk-thumbnail {
  display: inline-block;
  max-width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 4px;
  border: 1px solid #ddd;
  background: #fff; }

a.uk-thumbnail:focus, a.uk-thumbnail:hover {
  border-color: #aaa;
  background-color: #fff;
  text-decoration: none;
  outline: 0; }

.uk-thumbnail-caption {
  padding-top: 4px;
  text-align: center;
